@import "./grid";

@font-face {
  font-family: "Basier Square Monoregular";
  src: url("./fonts/basiersquaremono-regular-webfont.woff2") format("woff2"),
    url("./fonts/basiersquaremono-regular-webfont.woff") format("woff"),
    url("./fonts/basiersquaremono-regular-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

html,
body {
  margin: 0 !important;
  padding: 0 !important;
  top: 0;
}

* {
  font-family: "Basier Square Monoregular", monospace !important;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Basier Square Monoregular", monospace !important;

  /* background: #fef9f9 !important; */
  // background: #000000 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.no-scrollbar {
  // @media (min-width: $min-sm) {
  overflow: -moz-scrollbars-none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    /* remove scrollbar space */
    display: none;
    width: 0px;
    height: 0px;
  }
  // }
}

.hoverUp {
  cursor: pointer;
  :hover {
    transform: translateY(-0.5px);
    transition: transform ease 0.5s;
  }
}

.lds-ripple {
  display: inline-block;
  position: relative;
  div {
    position: absolute;
    border: 1px solid #000;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 2s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  div:nth-child(2) {
    animation-delay: -1s;
  }

  &.white {
    div {
      border: 1px solid #fff;
    }
  }
}
@keyframes lds-ripple {
  0% {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
}

.navbar-toggler {
  background: white !important;
}

.navbar-collapse {
  @media (max-width: 991px) {
    background: black;
    padding: 10px;
  }
}

.Toastify__progress-bar--default {
  background: #dcb84c !important;
}
